<template>
<!-- Modal -->
        <div class="modal fade" id="addNew" tabindex="-1" role="dialog" aria-labelledby="addNew" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" v-show="!editmode">Добавление направления</h5>
                    <h5 class="modal-title" v-show="editmode">Редактирование информации о направлении</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>

                <!-- <form @submit.prevent="createUser"> -->

                <form @submit.prevent="editmode ? updateDirection() : createDirection()">
                    <div class="modal-body">

                        <div class="form-group">
                            <label>Код</label>
                            <input v-model="form.code" type="text" name="name"
                                class="form-control" :class="{ 'is-invalid': form.errors.has('name') }">
                            <has-error :form="form" field="name"></has-error>
                        </div>

                        <div class="form-group">
                            <label>Профиль</label>
                            <select name="type" v-model="form.profile" id="type" class="form-control">
                                <option v-for="profile in profiles" :key="profile.id" :value="profile.id">{{profile.name}}</option>
                            </select>
                        </div>

                        <div class="form-group">
                            <label>Форма обучения</label>
                            <select name="type" v-model="form.study_form" id="type" class="form-control">
                                <option v-for="study_form in study_forms" :key="study_form.id" :value="study_form.id">{{study_form.name}}</option>
                            </select>
                        </div>

                        <div class="form-group">
                            <label>Специальность</label>
                            <select name="type" v-model="form.speciality" id="type" class="form-control">
                                <option v-for="speciality in specialities" :key="speciality.id" :value="speciality.id">{{speciality.name}}</option>
                            </select>
                        </div>

                        <div class="form-group" v-if="study_variants.length">
                            <label>Варианты обучения</label>
                            <span>
                                <button class="btn btn-primary btn-sm" @click.prevent="addNewVariant">
                                    Создать вариант обучения
                                </button>
                                <button class="btn btn-success btn-sm my-1" @click.prevent="pushVariant">
                                    Добавить вариант обучения
                                </button>
                            </span>
                            <select
                                name="type"
                                v-for="selectedVariants, index in form.study_variants"
                                :key="selectedVariants.id"
                                v-model="form.study_variants[index].id"
                                id="type"
                                class="my-2 form-control"
                            >
                                <option v-for="variant in study_variants" :key="variant.id" :value="variant.id">
                                    {{variant.years}} г. {{variant.months}} мес. {{variant.time_form.name}}
                                </option>
                            </select>
                        </div>

                        <div class="form-group" v-if="payment_forms.length">
                            <label>Формы оплаты</label>
                            <span>
                                <button class="btn btn-primary btn-sm" @click.prevent="addNewPaymentForm">
                                    Создать форму оплаты
                                </button>
                                <button class="btn btn-success btn-sm my-1" @click.prevent="pushPaymentForm">
                                    Добавить форму оплаты
                                </button>
                            </span>
                            <select
                                name="type"
                                v-for="paymentForm, index in form.payment_forms"
                                :key="paymentForm.id"
                                v-model="form.payment_forms[index].id"
                                id="type"
                                class="my-2 form-control"
                            >
                                <option v-for="form in payment_forms" :key="form.id" :value="form.id">
                                    {{form.name}}
                                </option>
                            </select>
                        </div>

                        <slot name="modal_additional_content"></slot>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">Закрыть</button>
                        <button v-show="editmode" type="submit" class="btn btn-success">Изменить</button>
                        <button v-show="!editmode" type="submit" class="btn btn-primary">Создать</button>
                    </div>
                </form>
                </div>
            </div>
        </div>
</template>

<script>
import StudyVariantCreate from './layout/StudyVariantCreate.vue'
import {notificationMixin} from '../mixins/notificationMixin'
export default {
    name: 'user-modal',
    components: {
        StudyVariantCreate
    },
    data() {
        return {
            study_forms: [],
            time_forms: [],
            profiles: [],
            specialities: [],
            study_variants: [],
            payment_forms: [],
        }
    },
    props: {
        editmode: {
            type: Boolean,
            default: () => false
        },
        form: {
            type: Object,
            default: () => {}
        },
        directionId: {
            type: Number,
        }
    },
    async mounted() {
        await axios.get(process.env.MIX_API_PATH + 'study_forms')
            .then(({data}) => this.study_forms = data.data)
            .catch(() => this.showFailMessage('Не удалось загрузить формы обучения'))
        await axios.get(process.env.MIX_API_PATH + 'payment_forms')
            .then(({data}) => this.payment_forms = data.data)
            .catch(() => this.showFailMessage('Не удалось загрузить финансовые формы договора'))
        await axios.get(process.env.MIX_API_PATH + 'profiles')
            .then(({data}) => this.profiles = data.data)
            .catch(() => this.showFailMessage('Не удалось загрузить профили подготовки'))
        await axios.get(process.env.MIX_API_PATH + 'specialities')
            .then(({data}) => this.specialities = data.data)
            .catch(() => this.showFailMessage('Не удалось загрузить специальности'))
        await axios.get(process.env.MIX_API_PATH + 'study_variants')
            .then(({data}) => this.study_variants = data.data)
            .catch(() => this.showFailMessage('Не удалось загрузить варианты обучения'))
    },
    methods: {
        addNewVariant() {
            this.$emit('close_direction_modal')
            this.$router.push('/study_variants')
        },
        addNewPaymentForm() {
            this.$emit('close_direction_modal')
            this.$router.push('/payment_forms')
        },
        pushVariant() {
            this.form.study_variants.push({id: 0})
        },
        pushPaymentForm() {
            this.form.payment_forms.push({id: 0})
        },
        async updateDirection() {
            await axios.put(process.env.MIX_DASHBOARD_PATH + `directions/${this.directionId}`, this.form).then(() => {
                this.showSuccessMessage('Направление было успешно изменено')
            }).catch(() => {
                this.showFailMessage('Не удалось изменить данные о направлении')
            }).finally(() => {
                this.$emit('close_direction_modal')
            })
        },
        async createDirection() {
            await axios.post(process.env.MIX_DASHBOARD_PATH + 'directions', this.form)
                .then(() => {
                    this.showSuccessMessage('Направление было успешно добавлено!')
                    this.$emit('close_direction_modal')
                }).catch(() => this.showFailMessage('Не удалось добавить направление обучения'))
                .finally(() =>  this.$emit('close_direction_modal'))
        }
    },
    mixins: [notificationMixin]
}
</script>
